@font-face {
  font-family: "Malssami815";
  src: url("./font/Malssami815.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "RixAcasia";
  src: url("./font/RixSinGoRegular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "S-CoreDream";
  src: url("./font/SCDream4.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "S-CoreDreamLight";
  src: url("./font/SCDream2.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

html, body {
  overscroll-behavior-y: contain;
}

body {
  margin: 0;
  font-family: "RixAcasia", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.alert {
  border: 2px solid black !important;
  box-sizing: border-box;
  font-size: 15px !important;
  border-radius: 20px !important;
  overflow: hidden;
}
